import React, { useEffect, useState } from 'react';
import { Divider, Typography, Col, Row } from 'antd';
import { injectIntl } from 'react-intl';
import QueueAnim from 'rc-queue-anim';
import { connect } from 'react-redux';

import config from './../../../config';
const { Paragraph } = Typography;
const { Text } = Typography;

const Services = ({ messages, lang }) => {
  let services = [
    messages.landing[config.default][2],
    messages.landing[config.default][3],
    messages.landing[config.default][4],
    messages.landing[config.default][5],
    messages.landing[config.default][6],
    messages.landing[config.default][7],
  ];
  const [array, setArray] = useState(services);
  useEffect(() => {
    main();
  }, [lang]);

  const main = () => {
    setArray(services);
  };
  console.log(lang);

  return (
    <div id="ssnn" className="container-fluid container-mw-xl" style={{ marginBottom: 40 }}>
      <Row gutter={[16, 16]} style={{ marginTop: '-80px' }}>
        {array.map((service, i) => {
          if (i === 3) {
            return (
              <React.Fragment>
                <Divider style={{ margin: '10px 0' }} />
                <Col xs={24} md={12} lg={8} key={i.toString()} style={{ padding: 10 }}>
                  <div className="box box-default mb-4">
                    <div className="box-body">
                      <div className="icon-card-v1 icon-card--center">
                        {/* <div className="icon--plain">
                  <Icon type={service.icon} className="text-primary" />
                </div> */}
                        <h4 className="icon-card__header">{service.text1}</h4>
                        <div className="icon-card__content" style={{ height: 166 }}>
                          <Paragraph ellipsis={{ rows: 5 }}>{service.text2}</Paragraph>
                        </div>
                        <Divider style={{ margin: '10px 0' }} />
                        <div className="icon-card__content" style={{ fontSize: 12, height: 100 }}>
                          <Text strong style={{ color: '#0368c6' }}>
                            {service.text3}
                          </Text>
                        </div>
                      </div>
                    </div>
                  </div>
                </Col>
              </React.Fragment>
            );
          }
          return (
            <Col xs={24} md={12} lg={8} key={i.toString()} style={{ padding: 10 }}>
              <div className="box box-default mb-4">
                <div className="box-body">
                  <div className="icon-card-v1 icon-card--center">
                    {/* <div className="icon--plain">
                  <Icon type={service.icon} className="text-primary" />
                </div> */}
                    <h4 className="icon-card__header">{service.text1}</h4>
                    <div className="icon-card__content" style={{ height: 166 }}>
                      <Paragraph ellipsis={{ rows: 5 }}>{service.text2}</Paragraph>
                    </div>
                    <Divider style={{ margin: '10px 0' }} />
                    <div className="icon-card__content" style={{ fontSize: 12, height: 100 }}>
                      <Text strong style={{ color: '#0368c6' }}>
                        {service.text3}
                      </Text>
                    </div>
                  </div>
                </div>
              </div>
            </Col>
          );
        })}
      </Row>
    </div>
  );
};

const Page = ({ intl: { messages }, lang }) => (
  <section className="chapter">
    <QueueAnim type="bottom" className="ui-animate">
      <div key="2">
        <Services messages={messages} lang={lang} />
      </div>
    </QueueAnim>
  </section>
);

const mapStateToProps = state => {
  return {
    lang: state.rootReducer.auth.lang,
  };
};

export default connect(mapStateToProps)(injectIntl(Page));
