import React, { useState } from 'react';
import { Layout, Icon, Dropdown, Button, Menu, message, Modal } from 'antd';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { injectIntl } from 'react-intl';
import { bindActionCreators } from 'redux';
import { changeLang } from './../../reducers/auth';
import Signin from './../../containers/Auth/Signin';
import config from './../../config';

const { Header } = Layout;

const Logo = styled.div`
  overflow: hidden;
  height: 64px;
  text-decoration: none;
  white-space: nowrap;
  img {
    height: 40px;
    margin-right: 16px;
  }
`;

const Modal2 = ({ lang, authChangeLang, messages }) => {
  const [visible, setVisible] = useState(false);

  const showModal = () => {
    setVisible(true);
  };

  const handleCancel = e => {
    setVisible(false);
  };

  const menu = (
    <Menu>
      <Menu.Item
        key="en"
        disabled={lang === 'en'}
        onClick={() => {
          authChangeLang('en');
        }}
      >
        {messages.languages.en}
      </Menu.Item>
      <Menu.Item
        key="es"
        disabled={lang === 'es'}
        onClick={() => {
          authChangeLang('es');
        }}
      >
        {messages.languages.es}
      </Menu.Item>
      <Menu.Item
        key="ja"
        disabled={lang === 'ja'}
        onClick={() => {
          authChangeLang('ja');
        }}
      >
        {messages.languages.ja}
      </Menu.Item>
    </Menu>
  );

  return (
    <div>
      <Dropdown overlay={menu}>
        <Button icon="global" style={{ marginLeft: 8, marginRight: 10 }}>
          {lang}
        </Button>
      </Dropdown>
      <Button icon="user" onClick={showModal}>
        Sign in
      </Button>
      <Modal visible={visible} onCancel={handleCancel} footer={null} className="custom-modal-v1">
        <Signin handleCancel={handleCancel} />
      </Modal>
    </div>
  );
};

const Section = ({ intl: { messages }, authChangeLang, lang }) => {
  return (
    <Header className="app-header" style={{ backgroundColor: '#ffffff' }}>
      <div className="app-header-inner bg-white container-fluid" style={{ maxWidth: `1140px` }}>
        <div className="header-left">
          <div className="list-unstyled list-inline">
            <Link to="" className="list-inline-item">
              {' '}
              <Icon type="menu-fold" className="list-icon" />{' '}
            </Link>
            <li className="list-inline-item search-box">
              <div className="search-box-inner">
                <Logo>
                  <img alt="logo" src={config[config.default].logo} />
                </Logo>
              </div>
            </li>
          </div>
        </div>

        <div className="header-right">
          <div className="list-unstyled list-inline">
            <div className="list-inline-item">
              <Modal2 messages={messages} authChangeLang={authChangeLang} lang={lang} />
            </div>
          </div>
        </div>
      </div>
    </Header>
  );
};

const mapStateToProps = state => {
  return {
    lang: state.rootReducer.auth.lang,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    authChangeLang: bindActionCreators(changeLang, dispatch),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(injectIntl(Section));
